import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationStart } from '@angular/router';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { ElementRef, ViewChild} from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatAutocompleteSelectedEvent, MatAutocomplete } from '@angular/material/autocomplete';
import { MatChipInputEvent } from '@angular/material/chips';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { UserService } from '../../../../../services/user/user.service';

@Component({
  selector: 'app-bc-info',
  templateUrl: './bc-info.component.html',
  styleUrls: ['./bc-info.component.css']
})
export class BcInfoComponent implements OnInit {
  public userData = {};
  technologiesList:any;
  languagesList:any;

  /** Common settings for autocomplete */
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  formControl = true;
  separatorKeysCodes: number[] = [ENTER, COMMA];

  /** specific data for autocomplete */
  languageCtrl = new FormControl();
  filteredLanguages: Observable<string[]>;
  filteredSkills: Observable<string[]>;
  languages: string[] = [];
  skills: string[] = [];
  allLanguages: string[] = [];
  allSkills: string[] = [];

  @ViewChild('langInput') langInput: ElementRef<HTMLInputElement>;
  @ViewChild('skillInput') skillInput: ElementRef<HTMLInputElement>;
  @ViewChild('auto') matAutocomplete: MatAutocomplete;
  
  constructor(private router: Router, private route: ActivatedRoute, private userService: UserService) { }

  ngOnInit() {
    this.userService.getTechnologiesList().subscribe((techListResponse:any) =>{
      if(techListResponse.isSuccess){
        this.technologiesList = techListResponse.response;
      }
    });
    this.userService.getLanguagesList().subscribe((langListResponse:any) =>{
      if(langListResponse.isSuccess){
        this.languagesList = langListResponse.response;
        let langArr = [];
        this.languagesList.forEach(lang => {
          langArr.push(lang.languageName);
        });
        this.allLanguages = langArr;
        console.log("this.allLanguages ----->", this.allLanguages);
        this.filteredLanguages = this.languageCtrl.valueChanges.pipe(
          startWith(null),
          map((fruit: string | null) => fruit ? this._filter(fruit) : this.allLanguages.slice()));
      }
    })

    this.userService.getSkillsList().subscribe((skillsListRespones:any) =>{
      if(skillsListRespones.is){

      }
    })
    
  }

  add(event: MatChipInputEvent): void {
    // Add fruit only when MatAutocomplete is not open
    // To make sure this does not conflict with OptionSelected Event
    if (!this.matAutocomplete.isOpen) {
      const input = event.input;
      const value = event.value;

      // Add our fruit
      if ((value || '').trim()) {
        this.languages.push(value.trim());
      }

      // Reset the input value
      if (input) {
        input.value = '';
      }

      this.languageCtrl.setValue(null);
    }
    console.log("selected languages==>", this.languages);
  }

  remove(fruit: string): void {
    const index = this.languages.indexOf(fruit);

    if (index >= 0) {
      this.languages.splice(index, 1);
    }
    console.log("selected languages==>", this.languages);
  }

  selected(event: MatAutocompleteSelectedEvent): void {
    this.languages.push(event.option.viewValue);
    this.langInput.nativeElement.value = '';
    this.languageCtrl.setValue(null);
  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.allLanguages.filter(fruit => {
      if(fruit){
        fruit.toLowerCase().indexOf(filterValue) === 0
      }
      return false;
    });
  }
  

}
