import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import { User } from './user';
import { Inject} from '@angular/core';
import { SESSION_STORAGE, StorageService } from 'angular-webstorage-service';
import { ADD_USER_URL, GET_USER_DETAILS_BY_EMAILID, 
  GET_TECHNOLOGIES_LIST_URL,GET_LANGUAGES_LIST_URL, GET_CITY_LIST_URL,
  GET_INSTITUTE_LIST_URL, GET_PROJECT_DOMAIN_LIST_URL ,GET_SKILLS_LIST_URL} from '../APIUrls';

const STORAGE_KEY = 'user-info';
@Injectable({
  providedIn: 'root'
})
export class UserService {
  public UserData: User;
  public userDataSubject = new Subject<User>();

  constructor(private http: HttpClient,@Inject(SESSION_STORAGE) private storage: StorageService) {
  }

   storeUserData(userData){
     console.log("in session set=-----------......>>>>>", userData);
     this.storage.set(STORAGE_KEY, userData)
   }

   getUserData() {
    return this.storage.get(STORAGE_KEY)
   }

  public getUserDataFromGmail() {
    return this.userDataSubject.asObservable();
  }

  addUser(user):Observable<any> {
    let addUserPayload = {
      "userName": user.name,
      "emailId": user.email,
      "role": {
        "id": 1
      }
    }
    return this.http.post(ADD_USER_URL, addUserPayload);
  }

  getUserByEmail(email): Observable<any> {
    let url = GET_USER_DETAILS_BY_EMAILID + `emailId=${email}&mobileNo=""`;
    return this.http.get(url);
  }

  getTechnologiesList(): Observable<any>{
    return this.http.get(GET_TECHNOLOGIES_LIST_URL);
  }

  getLanguagesList(): Observable<any>{
    return this.http.get(GET_LANGUAGES_LIST_URL);
  }
  
  getCitiesList(): Observable<any> {
     return this.http.get(GET_CITY_LIST_URL);
  }
  
  getProjectDomainList(): Observable<any> {
    return this.http.get(GET_PROJECT_DOMAIN_LIST_URL);
  }

  getInstituteList(): Observable<any> {
    return this.http.get(GET_INSTITUTE_LIST_URL);
  }

  getSkillsList(): Observable<any> {
    return this.http.get(GET_SKILLS_LIST_URL);
  }
  
}
